@import "@/scss/settings.scss";






































































































































































































































































































.home {
  min-height:px2html(570px);

  .music {
    width: px2html(40px);
    height: px2html(40px);
  }
  .music-group{
    position:relative;
    top:px2html(190px);
    display:flex;
    align-items: center;
    flex-direction: column;
    font-weight: 100;
    min-height:px2html(56px);
    .music-count{
      font-size:px2html(8px);
      color:#fff;
    }
  }

  .content-img {
    width: px2html(375px);
    position:absolute;
    top:px2html(0px);
    z-index:-1;
  }
  .icon-list {
    position: relative;
    top:px2html(170px);
    z-index:3;
    min-height:px2html(69px);
    display:flex;
    width:px2html(200px);
    justify-content: space-between;

    color:#FFFFFF;
    font-weight:100;
    img{
      width:px2html(30px);
      //background-color: #fff;
      height:px2html(30px);
      filter: invert(100%);
    }
    p{
      opacity: 0.8;
      position:relative;
      top:px2html(-10px);
      font-size:px2html(10px);
    }
  }
  .user{
    position: relative;
    top:px2html(95px);
    display:flex;
    justify-content: center;
    align-items: center;
    img{
      width:px2html(17px);
      height:px2html(17px);
      border-radius: 50%;

    }
    p{
      font-weight: 100;
      font-size:px2html(10px);
      color:#FFFFFF;
      margin-left: px2html(5px);
    }
  }
  .desc{
    position: relative;
    top:px2html(30px);
    font-size:px2html(12px);
    width:px2html(280px);
    color:#fff;
    position: relative;
    line-height: px2html(23px);
    max-height:px2html(60px);
    overflow: hidden;
    display:flex;
    font-weight: 100;
    //border-radius: px2html(16px);
    opacity: 0.5;
    .shadow{
      //box-shadow: inset px2html(0px) px2html(-40px) px2html(50px) #260f2b;
      position:absolute;
      z-index:3;
      bottom:px2html(-2px);
      width:px2html(280px);
      border-radius: px2html(8px);
      height:120%;
    }
    .desc-cont{
      //position:absolute;
      //top:px2html(20px);
      width:px2html(280px);
      mask:linear-gradient(to bottom, red, transparent );
    }
    .desc-cont:before{

    }
    .fuc{
    }
    .desc-cont-change{
      width:px2html(280px);
      //text-align: left;
      position:relative;
      left:px2html(0px);
    }
  }

  .open{
    position: relative;
    top:px2html(-45px);
    padding: px2html(15px);
    font-size:px2html(11px);
    color:#fff;
    padding-top:px2html(2px);
    padding-bottom:px2html(2px);
    border-radius: px2html(12px);
    border: 1px rgba(255,255,255,0.6) solid;
  }
}
