@import "@/scss/settings.scss";



















































































































































#app {
  //background: red;
  background: url(./assets/cosleep_share_bg_h5@3x.png);
  background-size: 100% 100%;
  position:fixed;
  background-repeat: repeat;
}

.bottom{
  height:px2html(40px);
  //overflow:hidden;
  background: #194260;
  width:px2html(375px);
  position:fixed;
  bottom:0%;
  z-index:9999;

  font-size:px2html(13px);
  color:#fff;
  .bottom-img{
    width:px2html(60px);
    position:relative;
    overflow:hidden;
    position:absolute;
    height:px2html(51px);
    left:px2html(10px);
    top:px2html(-12px);
    .bottom-hehe{
      position:absolute;
      height:px2html(90px);
      left:px2html(-40px);
      top:px2html(-2px);
      z-index:999;
    }
  }


  .bottom-center{
    font-weight:500;
    position:absolute;
    right:50%;
    top:50%;
    transform: translateX(50%) translateY(-50%);
    color: #3FA8F4;
    width:px2html(200px);
    font-size: px2html(18px);
  }
  .bottom-dl{
    position:absolute;
    right:px2html(30px);
    color: #fff;
    top:50%;
    border:1px #fff solid;
    padding:px2html(12px);
    padding-top:px2html(1px);
    padding-bottom:px2html(1px);
    border-radius:px2html(10px);
    font-size:px2html(13px);
    margin-right: px2html(10px);
    transform: translateX(50%) translateY(-50%);
  }
}

body{

  //-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  position:fixed;

  width:100%;
  height:100%;

}
